@import "variables.scss";
@import "mixins.scss";

.section5_table{
  width: 570px;
  background: #ffffff;
  border-collapse: collapse;
  border: 4px solid #fbf5f4;
  margin-bottom: 50px;
}

.section5_table_item{
  position: relative;
  border: 4px solid #fbf5f4;
  height: 140px;
  width: auto;
  padding: 20px;
  font-size: 18px;
  line-height: 35px;
  font-weight: lighter;
  text-align: left;
}

.section5_item-info{
  width: 170px;
  & span{
    color: $second-color;
    font-size: 24px;
  }
}

.section5_item-submit{
  position: absolute;
  right: 20px;
  top: calc(50% - 20px);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #fbf5f4;
  &:before, &:after{
    position: absolute;
    width: 20px;
    height: 2px;
    content: '';
    left: calc(50% - 10px);
    background: #c9c4c3;
  }
  &:before{
  }
  &:after{
    transform: rotate(-90deg);
  }
}

@media (max-width: 1200px){

  .section5_big-logo{
    display: none;
  }

  .section5_main-info{
    display: flex;
    flex-direction: column;
  }

  .section5_table{
    width: 500px;
  }

  .send-btn5{
    margin: 0 auto;
  }
}
