@import "variables.scss";
@import "mixins.scss";

.section9{
  overflow: hidden;
}

.section9_map{
  width: 55%;
  height: inherit;
}

.section9_vision-address{
  display: none;
}

.section9_superscription-container>span,
.section9_email-container>span{
  display: block;
  font-size: 14px;
  margin-top: 25px;
  margin-bottom: 15px;
}

.section9_superscription-container>p,
.section9_email-container>p{
  font-size: 18px;
  margin: 0;
}

.section9_markers{
  margin-top: 30px;
}

.section9_markers-container{
  display: flex;
  align-items: center;
  font-size: 14px;
}

.section9_markers-container span{
  display: inline-block;
  width: 30px;
  height: 30px;
  margin-right: 10px;
  border-radius: 50%;
}

.section9_markers-circle{
  background: url("../../img/location.png") no-repeat center, $main-gradient;
}

.section9_markers-envelope{
  background: url("../../img/envelope.png") no-repeat center, $main-gradient;
}

.section9_law{
  font-size: 14px;
  margin-top: 30px;
  margin-bottom: 40px;
}


@media (max-width: 1200px){

  .section9{
    position: relative;
  }

  .section9_map{
    width: 100%;
  }

  .section9_address{
    position: absolute;
    transition: all ease 2s;
    right: 30px;
    top: 100px;
    color: #f83e26;
    background: rgba(115, 115, 115, 0.38);
    border-radius: 28px;
  }

  .section9_vision-address{
    display: block;
    width: 30px;
    height: 30px;
    position: relative;
    user-focus: no;
    top: 10px;
    left: -50px;
    &:focus{
      outline: none;
    }
    &>span{
      display: block;
      transition: all ease 1s;
      position: relative;
      width: 30px;
      height: 3px;
      background: #ff6000;
    }
    &>span:nth-of-type(1){
      top: 3px;
      transform: rotate(45deg);
    }
    &>span:nth-of-type(2){
      transform: rotate(-45deg);
    }
  }

  .section9_address-anim{
    margin-right: -340px;
  }

  .section9_vision-address-anim{
    &>span:nth-of-type(1){
      top: 16px;
      transform: rotate(30deg);
    }
    &>span:nth-of-type(2){
      transform: rotate(-30deg);
    }
  }
}