@import "mixins.scss";
@import "variables.scss";

*{
  box-sizing: border-box;
}

body{
  width: 100%;
  font-family: Roboto, Arial, sans-serif;
  background: #FBF5F4;
  color: #000000;
  transition: all ease 250ms;
}

ul,li{
  list-style: none;
  margin: 0;
  padding: 0;
}

a{
  text-decoration: none;
}

button{
  background: none;
  border: none;
  cursor: pointer;
}

.section{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 90px;
  padding-right: 90px;
}

.small-logo{
  height: 30px;
  width: 48px;
}

.big-logo{
  max-height: inherit;
}

.send-btn{
  position: relative;
  height: 60px;
  width: 300px;
  background: $second-color;
  color: #ffffff;
  border-radius: 28px;
  font-size: 16px;
  transition: all ease 250ms;
  &:hover{
    -webkit-box-shadow: 0px 5px 17px -2px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 5px 17px -2px rgba(0,0,0,0.75);
    box-shadow: 0px 5px 17px -2px rgba(0,0,0,0.75);
  }
}

.section_btn-circle{
  position: absolute;
  left: 0;
  top: 0;
  width: 60px;
  height: 60px;
  background: $main-gradient;
  border-radius: 50%;
  &:before, &:after{
    content: '';
    position: absolute;
    top: 28px;
    width: 20px;
    height: 2px;
    background: #ffffff;
  }
  &:before{
    left: 20px;
  }
  &:after{
    left: 20px;
    transform: rotate(-90deg);
  }
}

.counter{
  display: flex;
  flex-direction: column;
  text-align: right;
}

.counter>span{
  color: $second-color;
  &:first-child{
    font-size: 72px;
  }
  &:last-child{
    font-size: 30px;
    padding-right: 10px;
  }
}


@media (max-width: 1200px) {

  .counter{
    display: none;
  }

  .section{
    position: relative;
    padding-left: 10px;
    padding-right: 10px;
    justify-content: center;
  }

  .small-logo{
    position: absolute;
    right: 10px;
    top: -30px;
  }
}