@import "variables.scss";
@import "mixins.scss";

.section2_list-variety{
  @include list;
}

.section2_list-marker{
  position: absolute;
  left: -50px;
  top: 15px;
  color: $second-color;
  font-size: $descr-size;
  transition: all ease 250ms;
}

.section2_main-info{
  max-width: 530px;
  &>span{
    color: $second-color;
    font-size: 48px;
  }
}

.section2_container{
  display: none;
}

.section2_container:nth-of-type(2){
  display: block;
}

.section2_title{
  font-size: 24px;
}

.section2_descr{
  font-size: $descr-size;
  line-height: 30px;
}

.section2_distance-descr{
  display: block;
  height: 15px;
}

.section2_move-left,
.section2_move-right{
  display: none;
}

@media (max-width: 1200px){
  
  .section2_main-info{
    position: relative;
  }

  .section2_list-variety,
  .section2_big-logo{
    display: none;
  }

  .section2_descr{
    font-size: $descr-sizeM;
  }

  .section2_move-cont{
    position: absolute;
    display: flex;
    right: 20px;
    top: 10px;
  }

  .section2_move-left,
  .section2_move-right{
    display: block;
    margin-right: 10px;
    width: 40px;
    height: 40px;
    background: #ff6000;
    border-radius: 50%;
    position: relative;
    &::before{
      position: absolute;
      content: "";
      left: 12px;
      top: 12px;
      width: 15px;
      height: 15px;
      background: white;
      transform: rotate(45deg);
    }
    &::after{
      position: absolute;
      content: "";
      left: 20px;
      top: 8px;
      width: 15px;
      height: 25px;
      background: #ff6000;
    }
  }

  .section2_move-right::after{
    left: 5px;
  }
}