@import "variables.scss";
@import "mixins.scss";

.section7_client-list{
  @include list;
  margin-right: -300px;
  margin-top: -300px;
  position: relative;
}

.section7_list-marker{
  position: absolute;
  color: $second-color;
  left: -55px;
  top: 14px;
  transition: all ease 250ms;
}

.section7_client-info{
  display: none;
  max-width: 530px;
}

.section7_client-info:nth-of-type(2){
  display: block;
}

.section7_client-top-info{
  display: flex;
  align-items: flex-end;
  &>img{
    width: 180px;
    height: 180px;
    margin-right: 40px;
  }
}

.section7_client-name{
  font-size: 30px;
  margin-bottom: 10px;
}

.section7_client-age{
  font-size: 18px;
  margin-bottom: 0;
}

.section7_client-review{
  margin-top: 30px;
  font-size: 18px;
  line-height: 30px;
  &>span{
    display: block;
    height: 15px;
  }
}

@media (max-width: 1200px){

  .section7_client-list{
    display: none;
  }

  .section7_big-logo{
    display: none;
  }

  .section7_client-review{
    font-size: $descr-sizeM;
  }

  .section7_client-top-info{
    position: relative;
    flex-direction: column;
    align-items: start;
  }

  .section7_move-left,
  .section_move-right{
    top: 60px;
    z-index: 2;
  }
}