@import "variables.scss";
@import "mixins.scss";

.main-footer{
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 60px;
  padding-bottom: 60px;
  padding-left: 90px;
  padding-right: 90px;
  color: black;
  transition: all ease 250ms;
}

.social{
  display: flex;
  justify-content: space-between;
  &>li>a{
    margin-right: 30px;
    color: black;
    font-size: 18px;
  }
}

.next-page{
  display: flex;
  justify-content: space-between;
}

.next-page_info{
  margin-right: 10px;
  font-size: 18px;
}

.next-page_btn{
  width: 40px;
  height: 40px;
  background: #ff6000;
  border-radius: 50%;
  margin-top: 5px;
  position: relative;
  &::before{
    position: absolute;
    content: "";
    left: 12px;
    top: 10px;
    width: 15px;
    height: 15px;
    background: white;
    transform: rotate(45deg);
  }
  &::after{
    position: absolute;
    content: "";
    left: 10px;
    top: 7px;
    width: 23px;
    height: 10px;
    background: #ff6000;
  }
}

.main-footer_counter{
  display: none;
}

@media (max-width: 1200px) {

  .main-footer{
    padding: 10px;
  }

  .main-footer_counter{
    display: flex;
    flex-direction: column;
    text-align: right;
  }

  .main-footer_counter>span{
    color: $second-color;
    &:first-child{
      font-size: 24px;
    }
    &:last-child{
      font-size: 14px;
      padding-right: 10px;
    }
  }

  .next-page_info{
    display: none;
  }
}