@import "variables.scss";
@import "mixins.scss";

.section4_list-trainer{
  @include list;
}

.section4_list-marker{
  left: -70px;
}

.section4_trainer-info{
  position: relative;
  max-width: 470px;
}

.section4_trainer-name{
  font-size: 48px;
  margin: 0;
}

.section4_trainer-info{
  font-size: 24px;
}

.section4_trainer-descr{
  margin-top: 45px;
  font-size: $descr-size;
  line-height: 30px;
  &>span{
    display: block;
    height: 15px;
  }
}

@media (max-width: 1200px) {

  .section4{
    flex-direction: column;
    margin-bottom: 100px;
  }

  .section4_big-logo{
    height: 300px;
    margin-top: 115px;
    margin-bottom: 40px;
  }

  .section4_list-trainer{
    display: none;
  }

  .section4_trainer-name,
  .section4_trainer-age {
    text-align: center;
  }

  .section4_trainer-descr{
    font-size: $descr-sizeM;
  }

}