@import "variables.scss";
@import "mixins.scss";

.section6_table{
  background: #ffffff;
  border-collapse: collapse;
  border: 4px solid #fbf5f4;
}

.section6_table-line>th, .section6_table-line>td{
  border: 2px solid #fbf5f4;
  font-size: 14px;
  font-weight: lighter;
  width: 200px;
  height: 75px;
  text-align: center;
}

.section6_table-line>th{
  color: $second-color;
  font-weight: 600;
}

.section6_table-empty{
  background: #fbf5f4;
}
