@import "variables.scss";
@import "mixins.scss";

.section1_greeting{
  position: relative;
}

.section1_vertical-text{
  position: absolute;
  left: -75px;
  top: 100px;
  font-size: 24px;
  color: $second-color;
  transform: rotate(-90deg);
}

.section1_main-title{
  font-size: 200px;
  margin: 0;
  color: $second-color;
}

.section1_invite{
  display: flex;
  align-items: center;
}

.section1_invite-btn{
  position: relative;
  width: 60px;
  height: 60px;
  background: linear-gradient(0deg, rgba(247,141,107,1) 0%, rgba(250,112,147,1) 100%);
  border-radius: 50%;
  &:before, &:after{
    content: '';
    position: absolute;
    width: 20px;
    height: 2px;
    background: #ffffff;
  }
  &:before{
    left: 20px;
  }
  &:after{
    left: 20px;
    transform: rotate(-90deg);
  }
}

.section1_invite-title{
  font-size: $descr-size;
  margin-left: 20px;
  max-width: 330px;
}

@media (max-width: 1200px) {

  .section1{
    flex-direction: column;
  }

  .section1_big-logo{
    height: 300px;
    margin-top: 55px;
    margin-bottom: 40px;
  }

  .section1_main-title{
    font-size: 130px;
    text-align: center;
  }

  .section1_vertical-text{
    font-size: 18px;
    left: -15px;
    top: 65px;
  }

  .section1_invite-title{
    font-size: $descr-sizeM;
  }
}