@import "variables.scss";
@import "mixins.scss";

.main-header{
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding-top: 60px;
  padding-bottom: 60px;
  padding-left: 90px;
  padding-right: 90px;
  font-size: 18px;
  color: black;
  transition: all ease 350ms;
}

.main-nav_list{
  @include list;
  z-index: 2;
  position: absolute;
  display: none;
  left: 100px;
  top: 70px;
}

.header_menu{
  position: relative;
  cursor: pointer;
  &:before, &:after{
    position: absolute;
    content: '';
    background: $second-color;
    width: 20px;
    height: 2px;
    transition: all ease 250ms;
  }
  &:before{
    left: -28px;
    top: 3px;
  }
  &:after{
    left: -28px;
    top: 12px;
  }
}

.vision-menu{
  display: block;
}

.header_menu-affect{
  &:before{
    top: 8px;
    transform: rotate(45deg);
  }
  &:after{
    transform: rotate(-45deg);
    top: 8px;
  }
}

.header_title{
  position: relative;
  &::before{
    position: absolute;
    content: "";
    left: -12px;
    top: 7px;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background: #ff6000;
  }
}

@media (max-width: 1200px) {

  .main-header{
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 40px;
    padding-right: 10px;
  }

  .header_title, .header_phone{
    display: none;
  }
}