@import "variables.scss";
@import "mixins.scss";

.section8_main-info{
  max-width: 440px;
}

.section8_title{
  font-size: 48px;
  max-width: 340px;
  margin-bottom: 30px;
  &>span{
    font-size: 80px;
    color: $second-color;
  }
}

.section8_descr{
  font-size: $descr-size;
  line-height: 30px;
}

.section8_form{
  display: flex;
  flex-direction: column;
  &>input{
    border: none;
    background: none;
    height: 60px;
    width: 100%;
    border-bottom: 2px solid #c9c4c3;
    margin-bottom: 20px;
    font-size: 16px;
  }
}

.section8-send_btn{
  margin-top: 30px;
}

.section8_agreement{
  font-size: 12px;
  max-width: 300px;
}


@media (max-width: 1200px){

  .section8_big-logo{
    display: none;
  }

  .section8_descr{
    font-size: $descr-sizeM;
  }
}